import { Button } from "antd";

const isValidateFields = async (form) => {
  return await form
    .validateFields()
    .catch((values) => false)
    .then((values) => Boolean(values));
};

export const DEFAULT_MODAL_BTN = {
  add: (closeModal, form) => (
    <Button
      onClick={async () => {
        (await isValidateFields(form)) && closeModal();
      }}
      htmlType="submit"
      type="primary"
    >
       
    </Button>
  ),
  edit: (closeModal) => (
    <Button onClick={() => closeModal()} htmlType="submit" type="primary">
      
    </Button>
  ),
  cancel: (closeModal, form) => (
    <Button
      onClick={() => {
        closeModal();
        form.resetFields();
      }}
      ghost
      type="primary"
    >
      
    </Button>
  ),
  ok: (closeModal) => (
    <Button onClick={() => closeModal()} type="primary">
      
    </Button>
  ),
};
